/* ==========
VARIABLES AND MIXINS
========== */

// Main:
$primary-1:#171b31;

// Accent
$accent-1: #8f0e3e; //red
$accent-2: #278864; //forrest green
$accent-3: #2a43ca; //blue
$accent-4: #d09a35; //orange

// Tints:
$tint-1: #007D8A;

// Greys:
$grey-1: #333;


// Typography
@mixin smooth {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}


// Layout
@mixin flex-column {
    display: flex;
    flex-direction: column;
}

@mixin flex-row {
    display: flex;
    flex-direction: row;
}


// Transitions
@mixin transition02 {
    transition: all .2s ease-out;
}