/* ==========
LANDING PAGE 
========== */

// IEP
.folio-landing.modify-iep {
    justify-content: center;
    align-items: flex-start;
    width: 80vw;
    padding: 4rem 0 4rem 8rem;
    background: rgb(214, 233, 208);
    background: linear-gradient(90deg,
            rgba(214, 233, 208, 1) 0%,
            rgba(168, 209, 240, 1) 100%);
    position: relative;

    .content {
        width: 100%;
        max-width: 60rem;
    }

    .category {
        font-weight: 600;
        font-size: 1.8rem;
        line-height: 1;
        margin-bottom: 1.2rem;
        display: block;
    }

    .title {
        font-size: 6rem;
        line-height: 1.1;
        margin-top: 0;
        margin-bottom: 3.2rem;
    }

    .para {
        font-size: 1.8rem;
        line-height: 1.4;
    }

    .link {
        display: inline-block;
        padding: 1.2rem 4rem 1.2rem 0;
        border-bottom: 1px solid #fff;
    }

    .btn-row {
        margin-top: 4rem;
    }

    .logos {
        position: absolute;
        top: 0;
        right: 0;
        padding: 2.4rem 4rem;
        @include flex-row;
        justify-content: space-between;
        width: 100%;
        max-width: 24rem;

        .logoIngenio {
            margin-top: .6rem;
        }

    }

    .screen {
        position: absolute;
        top: 12rem;
        right: calc(-20vw + 16rem);
        z-index: 1;
        width: 72rem;
        height: auto;
        border-radius: 0.4rem;
        overflow: hidden;
        box-shadow: .2rem .2rem .8rem 0 rgba(00, 00, 00, .2);

        img {
            width: 100%;
            display: block;
        }
    }

    .thumb {
        position: absolute;
        bottom: 10rem;
        right: -.8rem;
        border: 8px solid #fff;
        border-radius: .8rem 0 0 .8rem;
    }
}